import React from 'react';
import recycle from './icons/recycle.svg';
import './App.css';
import Choose from './Choose';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      route: "start",
      tree: {
        c1: "organico",
        c2: "altro0",
        organico: {
          c1: "umido",
          c2: "verde",
          umido: {
            text: "va buttato nel bidone dell'umido"
          },
          verde: {
            text: "va buttato in discarica"
          },
        },
        altro0: {
          c1: "vetro",
          c2: "altro1",
          vetro: {
            text: "va buttato bel cestino del vetro"
          },
          altro1: {
            c1: "carta",
            c2: "altro2",
            carta: {
              text: "va gettato nel bidone della carta"
            },
            altro2: {
              c1: "multipack",
              c2: "secco",
              multipack: {
                text: "va gettato nel multipack"
              },
              secco: {
                text: "va gettato nel secco"
              }
            }
          },
        }
      },
      c1: "organico",
      c2: "altro0",
      status: {
        fine: false,
        text: ""
      }
    };
  }

  reroute = (route) => {
    this.setState({
      route: route
    });
  }

  goDeep = (c) => {
    var c1 = "";
    var c2 = "";
    switch (c) {
      case "organico":
        c1 = "umido";
        c2 = "verde";
        break;

      case "umido":
        c1 = "umido";
        c2 = "umido";
        break;

      case "verde":
        c1 = "verde";
        c2 = "verde";
        break;

      case "altro0":
        c1 = "vetro";
        c2 = "altro1";
        break;

      case "vetro":
        c1 = "vetro";
        c2 = "vetro";
        break;

      case "altro1":
        c1 = "carta";
        c2 = "altro2";
        break;

      case "carta":
        c1 = "carta";
        c2 = "carta";
        break;

      case "altro2":
        c1 = "multipack";
        c2 = "secco";
        break;

      case "secco":
        c1 = "secco";
        c2 = "secco";
        break;

      case "multipack":
        c1 = "multipack";
        c2 = "multipack";
        break;
    }
    this.setState({
      c1: c1,
      c2: c2
    });
  }
  render() {
    switch (this.state.route) {
      case 'start':
        return (
          <div className="App">
            <header className="App-header">
              <img src={recycle} className="App-logo" alt="something goes wrong" />
              <p>
                <button className="button button1" onClick={() => {
                  this.setState({
                    route: "1",
                    c1: "organico",
                    c2: "altro0",
                  });
                }}>Inizia!</button>
              </p>
              <p>Se non trovi dove poter buttare il tuo rifiuto magari è perche è da portare in discarica!
                Se hai ulteriori dubbi vai sul dito di <a href="https://www.cemambiente.it/dove-lo-butto/">cem ambiente</a></p>
            </header>
          </div>
        );
      case '1':
        return (
          < Choose reroute={this.reroute} c1={this.state.c1} c2={this.state.c2} goDeep={this.goDeep} status={this.state.status} />
        );
    }
  }
}

export default App;
