import React from "react";
import './choose.css';
import './App.css'
import organic from './icons/organic.svg';
import glass from './icons/glass-bin.svg';
import plastic from './icons/plastic.svg';
import paper from './icons/paper-bin.svg';
import apple from './icons/apple.svg';
import sprout from './icons/sprout.svg';
import share from './icons/share.svg';
import reduce from './icons/reduce.svg';
class Choose extends React.Component {
    constructor(props) {
        super(props);
        //<p>1st Choose</p>
        //<button onClick={() => this.props.reroute("start")}>indietro</button>
    }
    checker = (a) => {
        if (a.includes('altro')) {
            return "ALTRO";
        } else {
            return a.toUpperCase();
        }
    }

    retriever = (a) => {
        switch (a) {
            case "ORGANICO":
                return (
                    <img src={organic} style={{ height: "20vh", padding: "10px" }}></img>
                );
                break;
            case "VETRO":
                return (
                    <img src={glass} style={{ height: "20vh", padding: "10px" }}></img>
                );
                break;
            case "MULTIPACK":
                return (
                    <img src={plastic} style={{ height: "20vh", padding: "10px" }}></img>
                );
                break;
            case "CARTA":
                return (
                    <img src={paper} style={{ height: "20vh", padding: "10px" }}></img>
                );
                break;
            case "UMIDO":
                return (
                    <img src={apple} style={{ height: "20vh", padding: "10px" }}></img>
                );
                break;
            case "VERDE":
                return (
                    <img src={sprout} style={{ height: "20vh", padding: "10px" }}></img>
                );
                break;
            case "ALTRO":
                return (
                    <img src={share} style={{ height: "20vh", padding: "10px" }}></img>
                );
                break;
            case "SECCO":
                return (
                    <img src={reduce} style={{ height: "20vh", padding: "10px" }}></img>
                );
                break;
        }
    }

    infoRetriever = (a) => {
        switch (a) {
            case "VETRO":
                return (
                    <p>Nel vetro va buttato tutto ciò che è di vetro come bottigle e contenitori, anche con i relativi tappi (svuotati e puliti)</p>
                );
                break;
            case "MULTIPACK":
                return (
                    <p>Nel multipack va buttato tutto ciò che è di: plastica, alluminio, acciaio e tetrapack</p>
                );
                break;
            case "CARTA":
                return (
                    <p>Nella carta va buttato tutto ciò che è fatto di carta o cartone tranne: scontrini, carta per alimenti e carta forno</p>
                );
                break;
            case "UMIDO":
                return (
                    <p>Nell'umido vanno buttati scarti di cibo e tovaglioli sporchi</p>
                );
                break;
            case "VERDE":
                return (
                    <p>Il verde va buttato in discarica ed è composto da scarti di piante e vegetali</p>
                );
                break;
            case "SECCO":
                return (
                    <p>Nel secco va buttato tutto ciò che non è riciclabile come: spazzolini, rasoi, pannolini, radiografie, sontrini</p>
                );
                break;
        }
    }

    render() {
        if (this.props.c1 === this.props.c2) {
            return (
                <div>
                    <div className="row">
                        <div className="column3">
                            <p>
                                {this.checker(this.props.c1)}
                                {this.infoRetriever(this.checker(this.props.c1))}
                            </p>
                        </div>

                    </div>
                    <div className="row">
                        <div className="column2">
                            <button className="button" onClick={() => { this.props.reroute("start") }}>Indietro!</button>
                        </div>
                    </div>
                </div>
            );
        } else {
            return (
                <div>
                    <div className="row">
                        <div className="column" onClick={() => { this.props.goDeep(this.props.c1) }}>{this.checker(this.props.c1)}
                            {this.retriever(this.checker(this.props.c1))}
                        </div>
                        <div className="column" onClick={() => { this.props.goDeep(this.props.c2) }}>{this.checker(this.props.c2)}
                            {this.retriever(this.checker(this.props.c2))}
                        </div>
                    </div>
                    <div className="row">
                        <div className="column2">
                            <button className="button" onClick={() => { this.props.reroute("start") }}>Indietro!</button>
                        </div>
                    </div>
                </div>
            );
        }
    }
}
export default Choose; 